import React,{Component} from 'react';
import {DateTime} from 'luxon';
import { Emoji, emojiIndex } from 'emoji-mart'
import reactStringReplace from 'react-string-replace';

const MessageUserInfo = ({data}) => {
    const {user, created_at} = data;
    return (
        <div className="message-sender-info">
            <span className="message-user">
                {user.full_name}
            </span>
            <span className="message-time">
                {DateTime.fromSQL(created_at).toLocaleString(DateTime.TIME_24_SIMPLE)}
            </span>
        </div>
    )
}

const MessageUserAva = ({data}) => {
    const {user} = data;
    return (
        <div className="user-ava">
            <img src={user.ava} />
        </div>
    );
}

let prev_user = null;
let prev_chat = null;

export default class Message extends Component{

    componentDidMount()
    {
        if(prev_chat !== this.props.message.conversation_id)
        {
            prev_chat = this.props.message.conversation_id;
            prev_user = null;
        } // end if
    }
    
    placeEmoji( message )
    {
        const emojies = message.match(/\:[\S]*\:/g);
        
        if(!emojies)
        {
            return message;
        } // end if
        
        const result = emojies.filter(emoji => {
            const matches = emojiIndex.search( emoji.replace(/\:/g, '') ).filter(emo => emo.colons === emoji)
            
            return matches.length;
        }).map((emo, i) => {
            return {
                emoji: <Emoji key={i} size={16} emoji={emo} set="google" />,
                index: message.indexOf(emo),
                length: emo.length
            }
        });   
        
        let r = [];
        for(let i=0; i<result.length; i++)
        {
            // current to insert
            const emo = result[i];

            // first item in message is not emojit
            if(emo.index !== 0)
            {
                if(i === 0)
                {
                    r.push( message.substring( 0, emo.index ) )
                }
                else
                {
                    // prev emoji; to calculate previous emoji start index
                    const pemo = result[i-1];
                    r.push( message.substring( pemo.index + pemo.length, emo.index  ) );
                } // end if
            }// end if
            r.push( emo.emoji );
        }
        
        return r;
    } // placeEmoji

    render(){
        const {message} = this.props;
        
        let show_info = true;
        
        if( prev_user === message.user.username )
        {
            show_info = false;
        } 
        else
        {
            prev_user = message.user.username;
        } // end if
        

        
        return (
            <div className="chat-message">
                { show_info ? <MessageUserAva data={message} /> : <div style={{width:34}}></div> }
                <div className="message">
                    { show_info ? <MessageUserInfo data={message} /> : null }
                    <div className="message-content">
                        {this.placeEmoji(message.message)}
                    </div>
                </div>
            </div>
        )
    }
}