
import {
    MESSAGE_SENDED,
    MESSAGE_RECIEVED,
    CHATS_LOADED,
    CLOSE_CHAT,
    CHAT_CLOSED,
    INC_CHAT_UNREAD,
    CHAT_READED,
    MESSAGE_NOTIFIED,
    STATUS_BUSY,
    STATUS_ONLINE,
    STATUS_OFFLINE,
    SOUND_OFF,
    SOUND_ON
} from './events';

const initialState = {
    action: 'none',
    target: undefined,
    messenger: {
        friend: [],
        chat: []
    }
}

const reducer = (state = initialState, action) => {
    switch(action.type){
        case SOUND_OFF : {
            return {
                ...state,
                messenger: {
                    ...state.messenger,
                    m_sound: 'off'
                }
            }
        }
        case SOUND_ON : {
            return {
                ...state,
                messenger: {
                    ...state.messenger,
                    m_sound: 'on'
                }
            }
        }
        case STATUS_BUSY : {
            return {
                ...state,
                messenger: {
                    ...state.messenger,
                    m_status: 'busy'
                }
            }
        }
        case STATUS_ONLINE : {
            return {
                ...state,
                messenger: {
                    ...state.messenger,
                    m_status: 'online'
                }
            }
        }
        case STATUS_OFFLINE : {
            return {
                ...state,
                messenger: {
                    ...state.messenger,
                    m_status: 'offline'
                }
            }
        }

        case MESSAGE_SENDED : {
            return {
                ...state,
                action: null,
            }
        }
        case INC_CHAT_UNREAD : {
            return {
                ...state,
                action: null,
                messenger: {
                    chat: state.messenger.chat.map(chat => {                        
                        if(action.data === chat.hash_id)
                        {
                            chat.unread++;
                        } // end if

                        return chat;
                    }),
                    ...state.messenger
                }
            }
        }
        case CHAT_READED : {
            return {
                ...state,
                action: null,
                messenger: {
                    chat: state.messenger.chat.map(chat => {                        
                        if(action.data === chat.hash_id)
                        {
                            chat.unread = 0;
                        } // end if

                        return chat;
                    }),
                    ...state.messenger
                }
            }
        }
        case MESSAGE_RECIEVED : {
            return {
                action: MESSAGE_RECIEVED,
                target: action.data,
                messenger: {
                    ...state.messenger
                }
            }
        }
        case MESSAGE_NOTIFIED : {            
            return {
                ...state,
                action: null
            }
        }
        case CHATS_LOADED : {
            return {
                ...state,
                messenger: action.data
            }
        }
        case CLOSE_CHAT : {
            return {
                ...state,
                chatToClose: action.data,
                action: CLOSE_CHAT
            }
        }
        case CHAT_CLOSED : {
            return {
                ...state,
                chatToClose: null,
                action: null
            }
        }
        default: return {
            ...state
        }
    }
}

export default reducer;